// ** React Imports
import { Link } from 'react-router-dom'

import { X, Heart, Star } from 'react-feather'
import { Card, CardBody, CardText, Button, Badge, InputGroup, InputGroupAddon, Input, InputGroupText } from 'reactstrap'
import { useSelector } from 'react-redux'


const Cart = props => {
  // ** Props
  const { products, stepper, deleteCartItem, dispatch } = props

  const store = useSelector(state => state.ecommerce)

  const func1 = (id) => {
    const temp = [],
      taskTemp = []
    for (let i = 0; i < store.cart.length; i++) {
      if (id === store.cart[i]._id) {
        continue
      } else {
        temp.push(store.cart[i])
      }
    }

    for (let i = 0; i < store.tasks.length; i++) {
      if (id === store.tasks[i]._id) {
        taskTemp.push(store.tasks[i])
        taskTemp[i].isInCart = false
      } else {
        taskTemp.push(store.tasks[i])
      }
    }

    dispatch(deleteCartItem(temp, taskTemp))
  }


  // ** Render cart items
  const renderCart = () => {
    return store.cart.map((item, index) => {
      return (
        <Card key={item._id} className='ecommerce-card'>
          <CardBody>
            <div className='item-name'>
              <h6 className='mb-0'>
                {`Task-ID : 023${index}`}
              </h6>
            </div>
            <div className='item-name'>
              <h3 className='mb-1'>
                <Link to={`#`}>{item.title}</Link>
              </h3>
            </div>
            <span className='text-dark mb-1'>User Number : {item.userNumber}</span>

          </CardBody>
          <div className='item-options text-center'>
            <div className='item-wrapper'>
              <div className='item-cost'>
                <h4 className='text-info'>${item.budget}</h4>
              </div>
            </div>
            <Button className='mt-1 remove-wishlist' color='danger' onClick={() => func1(item._id)}>
              <X size={14} className='mr-25' />
              <span>Remove</span>
            </Button>
          </div>
        </Card>
      )
    })
  }

  return (
    <div className='list-view product-checkout'>
      <div className='w-75 m-auto'>
        {products.length ? renderCart() : <h4 style={{ margin: '22% 40% 16%' }}>Your cart is empty</h4>}
        {products.length > 0 && <Button.Ripple
          color='primary'
          classnames='btn-next place-order float-right'
          // block
          style={{ float: 'right' }}
          disabled={!products.length}
          onClick={() => stepper.next()}
        >
          Place Order
        </Button.Ripple>}
      </div>
    </div>
  )
}

export default Cart
