// ** Third Party Components
import { useState } from 'react'

import { useSelector } from 'react-redux'
import { UnsupportedChainIdError, useWeb3React } from '@web3-react/core'
import { parseUnits, formatUnits } from '@ethersproject/units'
import { toast } from 'react-toastify'
import Web3 from 'web3'

import { Form, Label, Input, Button, Card, CardHeader, CardTitle, CardBody, CardText, CustomInput } from 'reactstrap'

const Payment = props => {
  const { products, dispatch, selectBuyTasks } = props
  const { account, activate, library, active } = useWeb3React()
  const [depositMAmount, setDepositMAmount] = useState('10')

  const store = useSelector(state => state)

  const onDepositMetamask = async () => {
    if (active) {
      const web3 = new Web3(library.provider)

      const decimals = 18
      const amounti = parseUnits(depositMAmount, decimals)
      const balance = await web3.eth.getBalance(account)
      console.log(balance)
      if (formatUnits(balance, decimals) < depositMAmount) {
        return toast.error('Balance not enough.')
      }
      web3.eth.sendTransaction({ from: account, to: '0x6BB7cbb2965C108db26edC911045Fe27Ab599104', value: amounti })
        .on('confirmation', (data, receipt) => {
          if (receipt.status) {
            return depositMetamask(receipt, amounti)
          }
        })
    } else {
      toast.info("First, Connect your wallet")
    }

  }

  const selectBuyTask = async () => {
    const result = await onDepositMetamask()
    dispatch(selectBuyTasks(products, store.auth.userData))
  }

  return (
    <Form
      className='list-view product-checkout w-75 m-auto pt-2'
      onSubmit={e => {
        e.preventDefault()
      }}
    >
      <div className='payment-type'>
        <Card className="pl-2">
          <CardHeader className='flex-column align-items-start'>
            <CardTitle tag='h4'>Payment options</CardTitle>
            <CardText className='text-muted mt-25'>Be sure to click on correct payment option</CardText>
          </CardHeader>
          <CardBody>
            <h4 className='card-holder-name my-75'>{store.auth.userData.username}</h4>
            <CardBody>
              <div className='details-title'>Price of {store.ecommerce.cart.length} items :  <strong>$699.30</strong></div>
            </CardBody>
            <ul className='other-payment-options list-unstyled m-2'>
              <li className='py-50'>
                <CustomInput type='radio' label='Metamask' name='paymentMethod' id='payment-card' value='metamask' defaultChecked />
              </li>
              <li className='py-50'>
                <CustomInput type='radio' label='Paypal' name='paymentMethod' id='payment-net-banking' value='paypal' />
              </li>
            </ul>
            <hr className='my-2' />
            <Button className="btn-primary" color="primary" onClick={selectBuyTask} >Continue</Button>
          </CardBody>
        </Card>
      </div>
    </Form>
  )
}

export default Payment
